import style from './Main.module.scss'
import styleContainer from "../common/container.module.scss";
import {Nav} from "../Header/Nav/Nav";
import background from '../common/img/bg.jpg';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faWhatsapp, faInstagram} from '@fortawesome/free-brands-svg-icons';
import { faTelegram } from '@fortawesome/free-brands-svg-icons';
import { faVk } from '@fortawesome/free-brands-svg-icons';
import { faGithub } from '@fortawesome/free-brands-svg-icons';
import ReactTypingEffect from 'react-typing-effect';

export const Main = () => {
    const bg = {
        backgroundImage: `url(${background})`,
        backgroundSize: 'cover',
        backgroundPosition: '50% 0%'
    }
    return(
        <section className={style.main} style={bg} name={'main'}>
            <div className={styleContainer.container}>
                <div className={style.main__wrapper}>
                    <div className={style.text}>
                        <span className={style.welcome}>Welcome!</span>
                        <h1><span className={style.about}>I am </span>
                            <ReactTypingEffect className={style.vocation} cursorClassName={style.vocation} eraseDelay={1000} speed={100} eraseSpeed={50} eraseDelay={1000} typingDelay={500} text={['Web Designer', 'Front Development']}/>
                            {/*className={style.vocation}>Web Designer*/}
                        </h1>
                    </div>
                    <div className={style.social}>
                        <a className={style.social_icon} href="#" ><FontAwesomeIcon icon={faInstagram}/></a>
                        <a className={style.social_icon} href="#" ><FontAwesomeIcon icon={faTelegram}/></a>
                        <a className={style.social_icon} href="#" ><FontAwesomeIcon icon={faWhatsapp}/></a>
                        <a className={style.social_icon} href="#" ><FontAwesomeIcon icon={faVk}/></a>
                        <a className={style.social_icon} href="#" ><FontAwesomeIcon icon={faGithub}/></a>
                    </div>
                </div>
            </div>

        </section>
    )
}