import style from './Header.module.scss'
import styleContainer from '../common/container.module.scss'
import {Nav} from "./Nav/Nav";
import logo from '../common/img/logo_white.png';
import { Link, animateScroll as scroll } from "react-scroll";
import {createRef, useEffect} from "react";

export const Header = ()=>{
    const refComponent = createRef();
    let t;
    useEffect(() => {
        let top = refComponent.current.getBoundingClientRect().top;
        t=top
        console.log(top, "top");
    }, [t]);

    return(
        <div ref={refComponent} className={style.header}>
            <div className={styleContainer.container}>
                <div className={style.header__wrapper}>
                    <div className={style.logo}>
                        <a href='https://good-production.ru/'><img src={logo} alt=""/></a>
                    </div>
                    <Nav/>

                </div>
            </div>
        </div>
    )
}