import style from './Nav.module.scss';
import { Link, animateScroll as scroll } from "react-scroll";

export const Nav = ()=>{

    return(
        <>
            <div className={style.nav}>
                <Link className={style.menu} activeClass={style.nav_active} to="main" spy={true}
                      smooth={true} offset={-30} duration={500}>Home</Link>
                <Link className={style.menu} activeClass={style.nav_active} to="about" spy={true}
                      smooth={true} offset={-20} duration={500}>About</Link>
                <Link className={style.menu} activeClass={style.nav_active} to="services" spy={true}
                      smooth={true} offset={-20} duration={500}>Services</Link>
                <Link className={style.menu} activeClass={style.nav_active} to="portfolio" spy={true}
                      smooth={true} offset={-20} duration={500}>Portfolio</Link>
                <Link className={style.menu} activeClass={style.nav_active} to="contact" spy={true}
                      smooth={true} offset={-20} duration={500}>Contact</Link>
            </div>
            <div className={style.nav_mobile}>

            </div>
        </>

    )
}